import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Router } from '@angular/router';

import { Observable, Subject } from 'rxjs';

import dayjs from 'dayjs';

import { ApiService } from '@api/api.service';
import { CompanyService } from '@company/company.service';

import {
  FX_CONTRACT_STATUS,
  IFxContract,
  IUploadingFile,
} from '@shared/models';
import { FilesService } from '@shared/services';
import { ListService } from '@shared/services/list.service';

import { CONTRACT_TYPE } from '../models';
import { share } from 'rxjs/operators';
import { environment } from '@environments/environment';

@Injectable({
  providedIn: 'root',
})
export class ContractsService {
  contractsSubmitted$ = new Subject();

  constructor(
    private companyService: CompanyService,
    private apiService: ApiService,
    private filesService: FilesService,
    private listService: ListService,
    private router: Router,
    private httpClient: HttpClient
  ) {}

  getContractsSubmitted() {
    return this.contractsSubmitted$.asObservable();
  }

  mapRemainderMessage(contract: IFxContract) {
    if (contract.remainder?.amount <= 0) {
      return 'AMOUNT_CLOSED';
    }

    if (dayjs().isAfter(contract.dateTo, 'date')) {
      return 'HAS_EXPIRED';
    }

    return null;
  }

  async submitContracts(
    files: IUploadingFile[],
    iban: string,
    shouldNavigateToContracts: boolean = false
  ) {
    const url = this.mapApiUrl();
    const body = {
      commissionWithdrawalIban: iban,
      attachments: files
        .filter((item) => {
          return !item.error && item.file;
        })
        .map((item) => {
          return {
            fileId: item.file.id,
          };
        }),
    };

    const contract = await this.apiService.post<IFxContract>(url, body);
    await this.checkAndSignIfNeeded(contract);

    this.contractsSubmitted$.next();

    if (shouldNavigateToContracts) {
      let route = 'contracts/outcoming/';

      if (
        this.companyService.isPrimaryAuthority() &&
        this.companyService.isSingleSignScheme()
      ) {
        route += CONTRACT_TYPE.PENDING;
      } else {
        route += CONTRACT_TYPE.ON_SIGN;
      }

      this.router.navigateByUrl(route);
    }

    return contract;
  }

  getContracts(type: CONTRACT_TYPE = null, extras: any = null) {
    const url = this.mapApiUrl();

    let params = new HttpParams();
    params = params.set('fetchAttachments', 'true');

    if (extras) {
      for (const key in extras) {
        if (Object.hasOwnProperty.call(extras, key)) {
          const value = extras[key];
          if (value) {
            params = params.append(key, value);
          }
        }
      }
    }

    switch (type) {
      case CONTRACT_TYPE.ON_SIGN:
        params = params.append('status', FX_CONTRACT_STATUS.ON_SIGN as string);
        break;

      case CONTRACT_TYPE.APPROVED:
        params = params.append('status', FX_CONTRACT_STATUS.REGED as string);
        // params = params.append('status', FX_CONTRACT_STATUS.CLOSED as string);
        break;

      case CONTRACT_TYPE.PENDING:
        params = params.append(
          'status',
          FX_CONTRACT_STATUS.REGISTERED as string
        );
        params = params.append(
          'status',
          FX_CONTRACT_STATUS.PROCESSING as string
        );

        break;

      case CONTRACT_TYPE.REJECTED:
        params = params.append('status', FX_CONTRACT_STATUS.REJECTED as string);
        break;

      default:
        break;
    }

    return this.apiService.get<IFxContract[]>(url, null, params);
  }

  async getContractCertificate(contractId: string) {
    const url = this.mapApiUrl(`${contractId}/certificate`);

    let headers = new HttpHeaders();
    headers = headers.set('Accept-File', 'application/pdf');
    headers = headers.set('Content-Type', 'application/octet-stream');

    const extras = {
      responseType: 'blob',
    };

    return this.apiService.get<any>(url, headers, null, extras);
  }

  async downloadContractCertificate(contract: IFxContract) {
    const fileBlob = await this.getContractCertificate(contract.id);
    await this.filesService.downloadFile(
      fileBlob,
      `${contract.contractor.name}.pdf`
    );

    return true;
  }

  sign(contract: IFxContract) {
    return this.listService.sign('fx-contracts', contract.id);
  }

  async checkAndSignIfNeeded(contract: IFxContract) {
    if (
      this.companyService.isSecondaryAuthority() ||
      (this.companyService.isPrimaryAuthority() &&
        this.companyService.isSingleSignScheme())
    ) {
      const result = await this.sign(contract);

      if (!result) {
        throw false;
      }

      return result;
    }

    return;
  }

  async getContractById(contractId: string) {
    const url = this.mapApiUrl(contractId);
    return this.apiService.get<IFxContract>(url);
  }

  getContractByIdObservable(contractId: string): Observable<IFxContract> {
    const url = this.mapApiUrl(contractId);
    return this.apiService.getObservable<IFxContract>(url);
  }
  public getActOfReconciliation(contractId: string){
    const url = this.mapApiUrl(`${contractId}/act-reconciliation`)
    const extras = {
      responseType: 'blob',
    };
    return this.apiService.get<any>(url, null, null, extras);
  }
  async downloadActOfReconciliationXlsx(contract: IFxContract) {
    const fileBlob = await this.getActOfReconciliation(contract.id);
    await this.filesService.downloadFile(
      fileBlob,
      `${contract.contractor.name}.xls`
    );

    return true;
  }
  private mapApiUrl(path: string = null) {
    const suffix = path ? `/${path}` : '';
    return `companies/${this.companyService.currentId}/fx-contracts${suffix}`;
  }

}
