import { Location } from '@angular/common';
import { Component, OnDestroy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CompanyService } from '@company/company.service';
import { ContractsService } from '@contracts/services';
import {
  FX_CONTRACT_ATTACHMENT_TYPE,
  FX_CONTRACT_STATUS,
  IFxContract,
} from '@shared/models';
import { of, Subscription } from 'rxjs';
import { catchError, finalize, take } from 'rxjs/operators';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { NotifierService } from 'angular-notifier';
import { TranslateService } from '@ngx-translate/core';

@UntilDestroy()
@Component({
  selector: 'j-contracts-details',
  templateUrl: './contracts-details.component.html',
  styleUrls: ['./contracts-details.component.scss'],
})
export class ContractsDetailsComponent {
  contract: IFxContract;

  contractId: string;
  companyName: string;

  isLoading = false;

  constructor(
    private companyService: CompanyService,
    private contractsService: ContractsService,
    private location: Location,
    private notifier: NotifierService,
    private route: ActivatedRoute,
    private translate: TranslateService
  ) {
    this.route.params.pipe(take(1)).subscribe((params) => {
      this.contractId = params.id;
    });

    this.companyService.refresh$.pipe(untilDestroyed(this)).subscribe(() => {
      this.companyName = this.companyService.currentCompany.name;
      this.loadContract();
    });
  }

  onClose() {
    this.location.back();
  }

  get iconClassName() {
    switch (this.contract.status) {
      case FX_CONTRACT_STATUS.REGISTERED:
      case FX_CONTRACT_STATUS.PROCESSING:
        return 'text-warning';

      case FX_CONTRACT_STATUS.REGED:
        return 'text-success';

      case FX_CONTRACT_STATUS.REJECTED:
      case FX_CONTRACT_STATUS.CLOSED:
        return 'text-danger';
    }
  }

  onRetry() {
    this.loadContract();
  }

  loadContract() {
    this.isLoading = true;
    this.contractsService
      .getContractByIdObservable(this.contractId)
      .pipe(
        untilDestroyed(this),
        finalize(() => {
          this.isLoading = false;
        })
      )
      .subscribe((contract) => {
        this.contract = contract;
      });
  }
}
