import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { NotifierService } from 'angular-notifier';

import { FX_CONTRACT_STATUS, IFxContract } from '@shared/models';

import { ContractsService } from '../../services';
import { CONTRACT_TYPE } from '../../models';
import { TranslateService } from '@ngx-translate/core';

const UNK_COLVIR_CODES = ['01', '02'];

@Component({
  selector: 'j-contracts-approved',
  templateUrl: './contracts-approved.component.html',
  styleUrls: ['./contracts-approved.component.scss'],
})
export class ContractsApprovedComponent {
  headings = [
    'CONTRACT_NUMBER',
    'CONTRACT_NAME',
    'CONTRACT_AMOUNT',
    'CONTRACT_REMAINDER',
  ];

  translatePrefix = 'CONTRACTS.APPROVED';
  contractType = CONTRACT_TYPE.APPROVED;

  loadingIds = [];
  loadingXlsIds = [];

  constructor(
    private contractsService: ContractsService,
    private route: ActivatedRoute,
    private router: Router,
    private notifierService: NotifierService,
    private translateService: TranslateService
  ) {}

  mapTranslate(value: string) {
    return `${this.translatePrefix}.${value}`;
  }

  hasUnk(contract: IFxContract) {
    return (
      contract.status == FX_CONTRACT_STATUS.REGED &&
      UNK_COLVIR_CODES.includes(contract.type.colvirCode)
    );
  }

  mapRemainderMessage(contract: IFxContract) {
    const remainderMessage =
      this.contractsService.mapRemainderMessage(contract);
    return remainderMessage ? this.mapTranslate(remainderMessage) : null;
  }

  onContractClick(contract: IFxContract) {
    this.router.navigate([contract.id], {
      relativeTo: this.route.parent.parent,
    });
  }

  async onDownload(event: Event, contract: IFxContract) {
    event.stopPropagation();

    if (this.loadingIds.includes(contract.id)) {
      return;
    }

    this.loadingIds.push(contract.id);

    try {
      await this.contractsService.downloadContractCertificate(contract);
    } catch (error) {
      this.notifierService.notify(
        'error',
        this.translateService.instant('ERRORS.DOWNLOAD_ERROR')
      );
    } finally {
      const index = this.loadingIds.indexOf(contract.id);
      this.loadingIds.splice(index, 1);
    }
  }
  async onXlsDownload(event: Event, contract: IFxContract) {
    event.stopPropagation();

    if (this.loadingXlsIds.includes(contract.id)) {
      return;
    }

    this.loadingXlsIds.push(contract.id);

    try {
      await this.contractsService.downloadActOfReconciliationXlsx(contract);
    } catch (error) {
      this.notifierService.notify(
        'error',
        this.translateService.instant('ERRORS.DOWNLOAD_ERROR')
      );
    } finally {
      const index = this.loadingXlsIds.indexOf(contract.id);
      this.loadingXlsIds.splice(index, 1);
    }
  }
}
