import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { formatDate } from '@angular/common';
import { Router } from '@angular/router';

import { Subscription } from 'rxjs';

import { TranslateService } from '@ngx-translate/core';
import { NotifierService } from 'angular-notifier';

import { FilesService } from '@shared/services';
import {
  IFxContract,
  IFxContractAttachment,
  PAYMENT_TYPE,
} from '@shared/models';

import { ContractsService } from '@contracts/services';

import {
  FeatureFlagsService,
  FEATURE_FLAG,
} from '@feature-flags/index';

import { paymentsRoutes } from '@payments/payments-routes';

const TRANSLATE_PREFIX = `CONTRACTS.DETAILS`;
const DATE_FORMAT = 'dd MMMM yyyy';

@Component({
  selector: 'j-contracts-info',
  templateUrl: './contracts-info.component.html',
  styleUrls: ['./contracts-info.component.scss'],
})
export class ContractsInfoComponent implements OnInit, OnDestroy {
  @Input() contract: IFxContract;
  data: { label: string; value: string; extraClass?: string }[];

  isPaymentCreatable = true;
  isContractErrored = false;
  isDownloading = false;
  isActReconciliationLoad = false;
  subscription = new Subscription();

  constructor(
    private notifierService: NotifierService,
    private translateService: TranslateService,
    private contractsService: ContractsService,
    private featureFlagsService: FeatureFlagsService,
    private filesService: FilesService,
    private router: Router
  ) {
    this.subscription.add(
      this.featureFlagsService.featureFlags$.subscribe((flags) => {
        this.checkFeatureFlags();
      })
    );
  }

  ngOnInit() {
    this.mapData();
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  onAttachmentDownload(event: Event, attachment: IFxContractAttachment) {
    event.preventDefault();

    this.filesService.downloadFileById(
      attachment.file.id,
      attachment.file.name
    );
  }
  onXlsDownload(){
    this.isActReconciliationLoad = true
    this.contractsService.downloadActOfReconciliationXlsx(this.contract).then(() => this.isActReconciliationLoad = false)
  }
  onCreatePayment() {
    this.router.navigate(
      [`/${paymentsRoutes.root}/${paymentsRoutes.foreignCurrency}`],
      {
        state: {
          resolvedPayment: {
            type: PAYMENT_TYPE.FOREIGN,
            isNewPayment: true,
            details: {
              paymentRecipient: null,
              foreignPaymentDetails: {
                withinContract: true,
                fxContract: this.contract,
                fxContractId: this.contract.id,
              },
            },
          },
        },
      }
    );
  }

  async onDownload() {
    if (this.isDownloading) {
      return;
    }

    this.isDownloading = true;
    try {
      await this.contractsService.downloadContractCertificate(this.contract);
    } catch (error) {
      this.notifierService.notify(
        'error',
        this.translateService.instant('ERRORS.DOWNLOAD_ERROR')
      );
    } finally {
      this.isDownloading = false;
    }
  }

  mapTranslate(prefix: string) {
    return `${TRANSLATE_PREFIX}.${prefix}`;
  }

  private mapData() {
    this.data = [];

    const errorMessage = this.contractsService.mapRemainderMessage(
      this.contract
    );

    if (errorMessage) {
      this.isContractErrored = true;
      this.data = [
        ...this.data,
        {
          label: this.mapTranslate('INFO'),
          value: this.translateService.instant(this.mapTranslate(errorMessage)),
          extraClass: 'text-danger',
        },
      ];
    }

    this.data = [
      ...this.data,
      {
        label: this.mapTranslate('TYPE'),
        value: this.contract.type.description,
      },
      {
        label: this.mapTranslate('DATE_FROM'),
        value: this.contract.dateFrom
          ? formatDate(
              this.contract.dateFrom,
              DATE_FORMAT,
              this.translateService.currentLang
            )
          : '-',
      },
      {
        label: this.mapTranslate('DATE_TO'),
        value: this.contract.dateTo
          ? formatDate(
              this.contract.dateTo,
              DATE_FORMAT,
              this.translateService.currentLang
            )
          : '-',
      },
    ];

    if (this.contract.registrationNumberContract) {
      this.data = [
        ...this.data,
        {
          label: this.mapTranslate('UNK'),
          value: this.contract.registrationNumberContract,
        },
      ];
    }

    if (this.contract.documentDate) {
      this.data = [
        ...this.data,
        {
          label: this.mapTranslate('UNK_DATE'),
          value: formatDate(
            this.contract.documentDate,
            DATE_FORMAT,
            this.translateService.currentLang
          ),
        },
      ];
    }

    this.data = [
      ...this.data,
      {
        label: this.mapTranslate('CURRENCY'),
        value: this.contract.amount.currency,
      },
      {
        label: this.mapTranslate('COUNTRY'),
        value: `${this.contract.contractor?.country?.code || ''} - ${
          this.contract.contractor?.country?.name || ''
        }`,
      },
      {
        label: this.mapTranslate('CITY'),
        value: this.contract.contractor?.cityName || '-',
      },
      {
        label: this.mapTranslate('BIC'),
        value: this.contract.contractor.bank?.code || '-',
      },
    ];
  }

  private async checkFeatureFlags() {
    this.isPaymentCreatable = !this.featureFlagsService.isDisabled(
      FEATURE_FLAG.FOREIGN_PAYMENT
    );
  }
}
