import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
  selector: 'j-contracts-error',
  templateUrl: './contracts-error.component.html',
  styleUrls: ['./contracts-error.component.scss'],
})
export class ContractsErrorComponent {
  @Input()
  label = 'CONTRACTS.ERRORED';

  @Output() retried = new EventEmitter();

  constructor() {}

  onRetry(event: Event) {
    event.preventDefault();
    this.retried.emit();
  }
}
