import { Component, Input, OnInit } from '@angular/core';
import { CompanyService } from '@company/company.service';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'j-contract-success-modal',
  templateUrl: './contract-success-modal.component.html',
  styleUrls: ['./contract-success-modal.component.scss'],
})
export class ContractSuccessModalComponent implements OnInit{
  description: string;

  constructor(
    public modal: NgbActiveModal,
    private companyService: CompanyService
  ) {}

  ngOnInit(){
    this.mapDescription();
  }

  dismissModal() {
    this.modal.dismiss();
  }

  closeModal() {
    this.modal.close();
  }

  private mapDescription() {
    if (this.companyService.isSecondaryAuthority()) {
      this.description = this.mapTranslate('SENT_FOR_PRIMARY_SIGN');
    } else if (
      this.companyService.isPrimaryAuthority() &&
      this.companyService.isSingleSignScheme()
    ) {
      this.description = this.mapTranslate('SENT_FOR_CHECK');
    } else {
      this.description = this.mapTranslate('SENT_FOR_SIGN');
    }
  }

  private mapTranslate(key: string) {
    return `CONTRACTS.SUCCESS_MODAL.${key}`;
  }
}
