import { Component } from '@angular/core';
import { Router } from '@angular/router';

import { Subscription } from 'rxjs';

import { CompanyService } from '@company/company.service';

import { IFxContract } from '@shared/models';
import { NavService } from '@shared/nav/nav.service';

import { ContractsService } from '../../services';
import { CONTRACT_TYPE } from '../../models';

@Component({
  selector: 'j-contracts-on-sign',
  templateUrl: './contracts-on-sign.component.html',
  styleUrls: ['./contracts-on-sign.component.scss'],
})
export class ContractsOnSignComponent {
  translatePrefix = 'CONTRACTS.LIST';
  contractType = CONTRACT_TYPE.ON_SIGN;

  isLoading: boolean = true;

  onSignForMeList: IFxContract[] = [];
  onSignForOthersList: IFxContract[] = [];

  errorText: string = null;

  subscription = new Subscription();

  constructor(
    private contractsService: ContractsService,
    private navService: NavService,
    private companyService: CompanyService,
    private router: Router
  ) {
    this.subscription.add(
      this.companyService.refresh$.subscribe((companyRefresh) => {
        const [user, company, isAuthorized] = companyRefresh;

        if (isAuthorized) {
          this.retrieveContracts();
        }
      })
    );

    this.subscription.add(
      this.contractsService.getContractsSubmitted().subscribe(() => {
        this.retrieveContracts();
      })
    );
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  async onSign() {
    await this.retrieveContracts();

    if (
      this.onSignForMeList.length == 0 &&
      this.companyService.isPrimaryAuthority() &&
      this.companyService.isSingleSignScheme()
    ) {
      this.router.navigateByUrl(`contracts/outcoming/${CONTRACT_TYPE.PENDING}`);
    }
  }

  onRetry() {
    this.navService.getContractBadges();
    this.retrieveContracts();
  }

  async retrieveContracts() {
    this.errorText = null;

    try {
      await Promise.all([
        this.retrieveOnSignForMe(),
        this.retrieveOnSignForOthers(),
      ]);

      // this.retrieved.emit({
      //   onSignForMe: this.onSignForMeList.length,
      //   onSignForOthers: this.onSignForOthersList.length,
      // });
    } catch (error) {
      this.errorText = error.description || error;
    } finally {
      this.isLoading = false;
    }
  }

  async retrieveOnSignForMe() {
    this.onSignForMeList = await this.contractsService.getContracts(
      CONTRACT_TYPE.ON_SIGN,
      {
        onSignForMe: true,
      }
    );
  }

  async retrieveOnSignForOthers() {
    this.onSignForOthersList = await this.contractsService.getContracts(
      CONTRACT_TYPE.ON_SIGN,
      {
        onSignForOthers: true,
      }
    );
  }

  mapTranslate(value: string) {
    return `${this.translatePrefix}.${value}`;
  }
}
