import {
  Component,
  Input,
  OnDestroy,
  OnInit,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';

import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import {
  FileSystemFileEntry,
  NgxFileDropComponent,
  NgxFileDropEntry,
} from 'ngx-file-drop';

import { CompanyService } from '@company/company.service';

import { AccountDropdownComponent, AccountsService } from '@accounts/index';
import { ACCOUNT_TYPE, RegularAccount } from '@accounts/account.model';

import { FilesService } from '@shared/services';
import { IUploadingFile } from '@shared/models';
import { NavService } from '@shared/nav/nav.service';
import { CURRENCY } from '@shared/currency/currency.model';

import { ContractsService } from '../../services/contracts.service';

const TRANSLATE_PREFIX = 'CONTRACTS.ADD_CONTRACT_MODAL';

@Component({
  selector: 'j-add-contract-modal',
  templateUrl: './add-contract-modal.component.html',
  styleUrls: ['./add-contract-modal.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class AddContractModalComponent implements OnInit, OnDestroy {
  @Input() shouldNavigateToContracts: boolean = false;

  @ViewChild('commissionAccountDropdown')
  commissionAccountDropdown: AccountDropdownComponent;
  @ViewChild('fileDrop') fileDrop: NgxFileDropComponent;
  acceptedFormats: string = '.pdf, .jpg, .jpeg, .doc, .png, .tif';

  uploadingFiles: IUploadingFile[] = [];

  isLoading: boolean = false;
  isAccountsLoading: boolean = false;

  selectedAccount: RegularAccount;
  accounts: RegularAccount[];
  submitLabel: string;

  constructor(
    public modal: NgbActiveModal,
    private companyService: CompanyService,
    private contractsService: ContractsService,
    private filesService: FilesService,
    private navService: NavService,
    private accountsService: AccountsService
  ) {
    this.companyService.refresh$.subscribe((companyRefresh) => {
      const [user, company, isAuthorized] = companyRefresh;

      if (isAuthorized) {
        this.retrieveAccounts();
      }
    });
  }

  ngOnInit() {
    this.uploadingFiles = [];
    this.mapSubmitLabel();
  }

  ngOnDestroy() {
    this.uploadingFiles = [];
  }

  onCommissionAccountSelect(account: RegularAccount) {
    this.selectedAccount = account;
  }

  public onFileDrop(files: NgxFileDropEntry[]) {
    for (const droppedFile of files) {
      if (droppedFile.fileEntry.isFile) {
        const fileEntry = droppedFile.fileEntry as FileSystemFileEntry;

        fileEntry.file((file: File) => {
          this.filesService.uploading$.next([file]);
        });
      }
    }
  }

  public onUploadingFilesUpdate(uploadingFiles: IUploadingFile[]) {
    this.uploadingFiles = uploadingFiles;
  }

  mapTranslate(value: string) {
    return `${TRANSLATE_PREFIX}.${value}`;
  }

  dismissModal(
    error: any = null,
    uploadingFiles: IUploadingFile[] = null,
    iban = null
  ) {
    this.modal.dismiss({ error, uploadingFiles, iban });
    this.uploadingFiles = [];
  }

  closeModal() {
    this.uploadingFiles = [];
    this.modal.close();
  }

  openFileSelector() {
    this.fileDrop.openFileSelector();
  }

  async submitFiles() {
    const canBeUploaded = this.filesService.checkIfCanBeUploadedAndNotify(
      this.uploadingFiles
    );

    if (!canBeUploaded || !this.selectedAccount) {
      return;
    }

    this.isLoading = true;

    try {
      await this.contractsService.submitContracts(
        this.uploadingFiles,
        this.selectedAccount.iban
      );

      this.navService.retrieveContractBadges();

      this.closeModal();
    } catch (error) {
      if (error) {
        this.dismissModal(
          error,
          this.uploadingFiles,
          this.selectedAccount.iban
        );
      } else {
        this.dismissModal(error);
      }
    } finally {
      this.isLoading = false;
    }
  }

  private mapSubmitLabel() {
    if (this.companyService.isSecondaryAuthority()) {
      this.submitLabel = this.mapTranslate('SIGN');
    } else if (
      this.companyService.isPrimaryAuthority() &&
      this.companyService.isSingleSignScheme()
    ) {
      this.submitLabel = this.mapTranslate('SIGN_AND_SEND');
    } else {
      this.submitLabel = this.mapTranslate('SENT_FOR_SIGN');
    }
  }

  async retrieveAccounts() {
    this.isAccountsLoading = true;

    try {
      const retrieved = await this.accountsService.getAccounts();

      this.accounts = retrieved.filter(
        (account) =>
          account.balance?.currency === CURRENCY.KZT &&
          account.accountType === ACCOUNT_TYPE.ACCOUNT
      );

      if (this.accounts.length) {
        this.selectedAccount = this.accounts[0];
        this.commissionAccountDropdown.selectAccount(this.selectedAccount);
      }
    } catch (error) {
    } finally {
      this.isAccountsLoading = false;
    }
  }
}
