import { Input } from '@angular/core';
import { Component, OnInit } from '@angular/core';
import { CONTRACT_TYPE } from '@contracts/models';
import { FX_CONTRACT_STATUS, IFxContract } from '@shared/models';

@Component({
  selector: 'j-contracts-status',
  templateUrl: './contracts-status.component.html',
  styleUrls: ['./contracts-status.component.scss'],
})
export class ContractsStatusComponent implements OnInit {
  @Input() contract: IFxContract;
  statusClassname: string;

  constructor() {}

  ngOnInit() {
    this.mapContractType();
  }

  private mapContractType() {
    switch (this.contract.status) {
      case FX_CONTRACT_STATUS.REGED:
        this.statusClassname = 'text-success';
        break;

      case FX_CONTRACT_STATUS.PROCESSING:
      case FX_CONTRACT_STATUS.REGISTERED:
        this.statusClassname = 'text-warning';
        break;

      case FX_CONTRACT_STATUS.REJECTED:
        this.statusClassname = 'text-danger';
        break;
    }
  }
}
