import { formatDate } from '@angular/common';
import {
  Component,
  EventEmitter,
  Inject,
  Input,
  LOCALE_ID,
  Output,
} from '@angular/core';

import { TranslateService } from '@ngx-translate/core';

import { IFxContract, IFxContractAttachment } from '@shared/models';
import { NavService } from '@shared/nav/nav.service';
import { FilesService } from '@shared/services';

import { CONTRACT_TYPE } from '../../models';

@Component({
  selector: 'j-contracts-group',
  templateUrl: './contracts-group.component.html',
  styleUrls: ['./contracts-group.component.scss'],
})
export class ContractsGroupComponent {
  @Input() title: string;
  @Input() list: IFxContract[];

  @Input() contractType: CONTRACT_TYPE;

  @Input() isDeletable: boolean = false;
  @Input() isSelectable: boolean = false;
  @Input() isSignable: boolean = false;

  @Output() scrolled = new EventEmitter();
  @Output() signed = new EventEmitter();

  translatePrefix = 'CONTRACTS.LIST';
  urlPath = 'fx-contracts';

  collapsedIds = [];

  modalPartialTitleFn = this.mapModalPartialTitle.bind(this);

  constructor(
    private navService: NavService,
    private translateService: TranslateService,
    @Inject(LOCALE_ID) private locale: string
  ) {}

  mapModalPartialTitle(contract: IFxContract) {
    const mapped = this.translateService.instant(
      this.mapTranslate('ITEM_TITLE'),
      {
        date: formatDate(contract.created, 'd MMMM yyyy', this.locale),
      }
    );

    return mapped ? `${mapped}:` : ``;
  }

  mapTranslate(key: string) {
    return `${this.translatePrefix}.${key}`;
  }

  onClick(contract: IFxContract) {
    contract.isCollapsed = !contract.isCollapsed;
  }

  onScroll() {
    this.scrolled.emit();
  }

  onClear() {
    this.navService.retrieveContractBadges();
  }

  onSign() {
    this.signed.emit();
  }
}
