import { Component, Input, OnInit } from '@angular/core';

import { TranslateService } from '@ngx-translate/core';

import { IFxContract, IFxContractAttachment } from '@shared/models';
import { FilesService } from '@shared/services';

@Component({
  selector: 'j-contracts-item',
  templateUrl: './contracts-item.component.html',
  styleUrls: ['./contracts-item.component.scss'],
})
export class ContractsItemComponent implements OnInit {
  @Input() contract: IFxContract;

  @Input() isDeletable: boolean = false;
  @Input() isSelectable: boolean = false;
  @Input() isSignable: boolean = false;

  translatePrefix: string = 'CONTRACTS.ITEM';
  maxAllowedAttachments = 10;

  constructor(
    private translateService: TranslateService,
    private filesService: FilesService
  ) {}

  ngOnInit() {}

  get attachments() {
    return this.contract.attachments.slice(0, this.maxAllowedAttachments);
  }

  get hasMoreAttachments() {
    return this.maxAllowedAttachments < this.contract.attachments.length;
  }

  get attachmentsMaxHeight() {
    if (!this.contract.isCollapsed) {
      return 0;
    }

    let maxHeight = this.attachments.length * 56;

    if (this.hasMoreAttachments) {
      maxHeight += 42;
    }

    return `${maxHeight}px`;
  }

  async onAttachmentDownload(event: Event, attachment: IFxContractAttachment) {
    event.stopPropagation();

    if (attachment.isLoading) {
      return;
    }

    attachment.isLoading = true;

    try {
      await this.filesService.downloadFileById(
        attachment.file.id,
        attachment.file.name
      );
    } catch (error) {
    } finally {
      attachment.isLoading = false;
    }
  }

  showMoreAttachments() {
    this.maxAllowedAttachments += 10;
  }

  mapAttachmentsTitle(contract: IFxContract) {
    return this.mapAttachmentsKey(
      'ATTACHMENTS_TITLE',
      contract.attachments?.length
    );
  }

  mapTranslate(key: string) {
    return `${this.translatePrefix}.${key}`;
  }

  mapMoreTitle() {
    return this.mapAttachmentsKey(
      'MORE',
      Math.min(
        this.contract.attachments.length - this.maxAllowedAttachments,
        10
      )
    );
  }

  private mapAttachmentsKey(key: string, itemsCount: number) {
    const params = {
      count: itemsCount,
    };

    let result = `${key}.${this.mapPluralKey(itemsCount)}`;

    return this.translateService.stream(this.mapTranslate(result), params);
  }

  private mapPluralKey(itemsCount: number) {
    switch (itemsCount % 20) {
      case 1:
        return 'ONE';
      case 2:
      case 3:
      case 4:
        return 'FEW';
      default:
        return 'OTHER';
    }
  }
}
