import {
  Component,
  ContentChild,
  Input,
  OnDestroy,
  OnInit,
  TemplateRef,
} from '@angular/core';

import { Subscription } from 'rxjs';

import { CompanyService } from '@company/company.service';
import { IFxContract } from '@shared/models';

import { ContractsService } from '../../services';
import { CONTRACT_TYPE } from '../../models';
import { NavService } from '@shared/nav/nav.service';

@Component({
  selector: 'j-contracts-list',
  templateUrl: './contracts-list.component.html',
  styleUrls: ['./contracts-list.component.scss'],
})
export class ContractsListComponent implements OnDestroy, OnInit {
  @Input() translatePrefix: string;
  @Input() contractType: CONTRACT_TYPE;

  @Input() onSignForMe: boolean;
  @Input() onSignForOthers: boolean;

  @ContentChild('itemTemplate', { static: false })
  itemTemplate: TemplateRef<any>;
  @ContentChild('headerTemplate', { static: false })
  headerTemplate: TemplateRef<any>;

  contractsList: IFxContract[];
  isLoading: boolean = false;
  errorText: string;

  subscription = new Subscription();

  constructor(
    private contractsService: ContractsService,
    private companyService: CompanyService,
    private navService: NavService
  ) {}

  ngOnInit() {
    const companySubscription = this.companyService.refresh$.subscribe(() => {
      this.loadContracts();
    });

    this.subscription.add(companySubscription);
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  mapTranslate(value: string) {
    return `${this.translatePrefix}.${value}`;
  }

  async loadContracts() {
    this.errorText = null;
    this.isLoading = true;

    try {
      let extras = null;

      if (this.onSignForMe) {
        extras = {
          onSignForMe: true,
        };
      }

      if (this.onSignForOthers) {
        extras = {
          onSignForOthers: true,
        };
      }

      this.contractsList = await this.contractsService.getContracts(
        this.contractType,
        extras
      );
      this.navService.retrieveContractBadges();
    } catch (error) {
      this.errorText = error.description || error.code || error || true;
    } finally {
      this.isLoading = false;
    }
  }
}
