import { Component, Input } from '@angular/core';
import { ERRORS } from '@api/errors';

import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { IUploadingFile } from '@shared/models';

import { ContractsService } from '../../services/contracts.service';

@Component({
  selector: 'j-contract-error-modal',
  templateUrl: './contract-error-modal.component.html',
  styleUrls: ['./contract-error-modal.component.scss'],
})
export class ContractErrorModalComponent {
  @Input() description: string;
  @Input() error: any;
  errorText: string;

  isLoading: boolean = false;

  @Input() iban: string;
  @Input() uploadingFiles: IUploadingFile[];

  constructor(
    public modal: NgbActiveModal,
    private contractsService: ContractsService
  ) {}

  ngOnInit() {
    this.errorText =
      this.error.code === ERRORS.FORBIDDEN
        ? 'CONTRACTS.ERROR_MODAL.FORBIDDEN'
        : 'CONTRACTS.ERROR_MODAL.GENERAL';
  }

  async submitContracts() {
    this.isLoading = true;
    try {
      await this.contractsService.submitContracts(this.uploadingFiles, this.iban);
      this.closeModal();
    } catch (error) {
    } finally {
      this.isLoading = false;
    }
  }

  dismissModal() {
    this.modal.dismiss();
  }

  closeModal() {
    this.modal.close();
  }
}
