import { Injectable } from '@angular/core';

import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

import {
  FeatureFlagsService,
  FEATURE_FLAG,
} from '@feature-flags/index';

import { IUploadingFile } from '@shared/models';
import { JModalService } from '@shared/services';

import { AddContractModalComponent } from '../modals/add-contract-modal/add-contract-modal.component';
import { ContractErrorModalComponent } from '../modals/contract-error-modal/contract-error-modal.component';
import { ContractSuccessModalComponent } from '../modals/contract-success-modal/contract-success-modal.component';

@Injectable({
  providedIn: 'root',
})
export class ContractsModalsService {
  constructor(
    private modalService: NgbModal,
    private jModalService: JModalService,
    private featureFlagsService: FeatureFlagsService
  ) {}

  async showAddContractModal(shouldNavigateToContracts: boolean = false) {
    const isAddingDisabled = await this.featureFlagsService.isDisabledPromise(
      FEATURE_FLAG.FX_CONTRACTS
    );

    if (isAddingDisabled) {
      this.jModalService.showMaintenanceModal();
      return;
    }

    const modalRef = this.modalService.open(AddContractModalComponent, {
      backdropClass: 'backdrop_light',
      windowClass: 'modal_light modal_dictionary',
      centered: true,
    });

    modalRef.componentInstance.shouldNavigateToContracts =
      shouldNavigateToContracts;

    try {
      await modalRef.result;
      this.showSuccessModal();
    } catch ({ error, uploadingFiles, iban }) {
      if (uploadingFiles) {
        this.showErrorModal(error, uploadingFiles, iban);
      }
    }
  }

  showSuccessModal() {
    this.modalService.open(ContractSuccessModalComponent, {
      backdropClass: 'backdrop_light',
      windowClass: 'modal_light modal_sm',
      centered: true,
    });
  }

  async showErrorModal(
    error: any,
    uploadingFiles: IUploadingFile[],
    iban: string
  ) {
    const modalRef = this.modalService.open(ContractErrorModalComponent, {
      backdropClass: 'backdrop_light',
      windowClass: 'modal_light modal_sm',
      centered: true,
    });

    (modalRef.componentInstance as ContractErrorModalComponent).iban = iban;
    (modalRef.componentInstance as ContractErrorModalComponent).uploadingFiles =
      uploadingFiles;
    (modalRef.componentInstance as ContractErrorModalComponent).error = error;

    try {
      await modalRef.result;
      this.showSuccessModal();
    } catch (error) {}
  }
}
