import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { combineLatest, Subscription } from 'rxjs';

import { IFxContract } from '@shared/models';
import { NavService } from '@shared/nav/nav.service';

import { CompanyService } from '@company/company.service';

import { ContractsService } from '../../services';
import { CONTRACT_TYPE } from '../../models';

@Component({
  selector: 'j-contracts-list-base',
  templateUrl: './contracts-list-base.component.html',
  styleUrls: ['./contracts-list-base.component.scss'],
})
export class ContractsListBaseComponent implements OnInit {
  contractType: CONTRACT_TYPE;

  isDeletable: boolean = false;
  isSelectable: boolean = false;
  isSignable: boolean = false;

  translatePrefix: string = 'CONTRACTS.LIST';
  contractsList: IFxContract[];
  isLoading: boolean = false;
  errorText: string;
  emptyLabel: string;

  subscription = new Subscription();

  constructor(
    private navService: NavService,
    private contractsService: ContractsService,
    private companyService: CompanyService,
    private route: ActivatedRoute
  ) {}

  ngOnInit() {
    this.subscription.add(
      combineLatest([this.route.data, this.companyService.refresh$]).subscribe(
        (values) => {
          const [data, companyRefresh] = values;

          this.contractType = data.contractType;
          this.mapListActions();
          this.mapEmptyLabel();

          this.retrieveContracts();
        }
      )
    );

    this.subscription.add(
      this.contractsService.getContractsSubmitted().subscribe(() => {
        this.retrieveContracts();
      })
    );
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  mapTranslate(value: string) {
    return `${this.translatePrefix}.${value}`;
  }

  async retrieveContracts() {
    this.errorText = null;
    this.isLoading = true;

    try {
      this.contractsList = await this.contractsService.getContracts(
        this.contractType
      );
      this.navService.retrieveContractBadges();
    } catch (error) {
      this.errorText = error.description || error.code || error || true;
    } finally {
      this.isLoading = false;
    }
  }

  private mapListActions() {
    switch (this.contractType) {
      case CONTRACT_TYPE.REJECTED:
        // this.isDeletable = true;
        this.isSelectable = true;
        break;

      default:
        break;
    }
  }

  private mapEmptyLabel() {
    let label = 'EMPTY.';

    switch (this.contractType) {
      case CONTRACT_TYPE.APPROVED:
        label += 'APPROVED';
        break;

      case CONTRACT_TYPE.REJECTED:
        label += 'REJECTED';
        break;

      case CONTRACT_TYPE.PENDING:
        label += 'PENDING';
        break;

      default:
        break;
    }

    this.emptyLabel = this.mapTranslate(label);
  }
}
