import { Component, Input, OnInit } from '@angular/core';
import { FX_CONTRACT_STATUS, IFxContract } from '@shared/models';
import dayjs from 'dayjs';
import { of } from 'rxjs';
import { TranslateService } from '@ngx-translate/core';
import { formatDate } from '@angular/common';

@Component({
  selector: 'j-contracts-date',
  templateUrl: './contracts-date.component.html',
  styleUrls: ['./contracts-date.component.scss'],
})
export class ContractsDateComponent implements OnInit {
  @Input() contract: IFxContract;
  translatePrefix: string = 'CONTRACTS.DATE';

  constructor(
    private translateService: TranslateService,
  ) {}

  ngOnInit() {}

  get tooltip() {
    if (!this.contract) {
      return;
    }

    switch (this.contract.status) {
      case FX_CONTRACT_STATUS.ON_SIGN:
      case FX_CONTRACT_STATUS.REGED:
      case FX_CONTRACT_STATUS.REGISTERED:
      case FX_CONTRACT_STATUS.PROCESSING:
        return this.mapTranslate('SENT');

      case FX_CONTRACT_STATUS.REJECTED:
        return this.mapTranslate('REJECTED');
    }
  }

  mapTranslate(key: string) {
    return `${this.translatePrefix}.${key}`;
  }

  public mapDateTime(date: Date) {
    const isToday = dayjs().isSame(dayjs(date), 'day');
    const isYesterday = dayjs().subtract(1, 'date').isSame(dayjs(date), 'day');

    if (isToday) {
      return this.translateService.get('STATEMENT.TRANSACTION.DATE.TODAY');
    } else if (isYesterday) {
      return  this.translateService.get('STATEMENT.TRANSACTION.DATE.YESTERDAY');
    } else {
      return of(this.convertToDayHourFormat(this.contract.created));
    }
  }

  private convertToDayHourFormat(date: Date) {
    const format = dayjs().isSame(this.contract.created, 'year')
      ? 'd MMMM HH:mm'
      : 'd MMMM yyyy HH:mm';
    return formatDate(this.contract.created, format, this.translateService.currentLang);
  }
}
