import { Component, OnDestroy, OnInit } from '@angular/core';

import { ContractsModalsService } from '@contracts/services';

import {
  FeatureFlagsService,
  FEATURE_FLAG,
} from '@feature-flags/index';
import { JModalService } from '@shared/services/j-modal.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'j-contracts-nav',
  templateUrl: './contracts-nav.component.html',
  styleUrls: ['./contracts-nav.component.scss'],
})
export class ContractsNavComponent implements OnDestroy, OnInit {
  isAddingDisabled: boolean = false;
  contractTabs = [
    {
      label: 'CONTRACTS.TABS.OUTCOMING',
      url: '/contracts/outcoming',
    },
    {
      label: 'CONTRACTS.TABS.INCOMING',
      url: '/contracts/incoming',
      soon: true,
    },
  ];

  subscription = new Subscription();

  constructor(
    private contractsModalsService: ContractsModalsService,
    private featureFlagsService: FeatureFlagsService
  ) {}

  ngOnInit() {
    this.checkFeatureFlag();

    this.subscription.add(
      this.featureFlagsService.featureFlags$.subscribe((flags) => {
        this.checkFeatureFlag();
      })
    );
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  showAddContractModal() {
    this.contractsModalsService.showAddContractModal(true);
  }

  private checkFeatureFlag() {
    this.isAddingDisabled = this.featureFlagsService.isDisabled(
      FEATURE_FLAG.FX_CONTRACTS
    );
  }
}
