import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import {
  ContractsDetailsComponent,
  ContractsOutcomingComponent,
  ContractsApprovedComponent,
  ContractsOnSignComponent,
  ContractsListBaseComponent,
} from './containers';

import { ContractsComponent } from './contracts.component';
import { CONTRACT_TYPE } from './models';

const routes: Routes = [
  {
    path: '',
    component: ContractsComponent,
    data: {
      title: 'CONTRACTS.TITLE',
    },
    children: [
      {
        path: 'outcoming',
        component: ContractsOutcomingComponent,
        children: [
          {
            path: CONTRACT_TYPE.ON_SIGN,
            component: ContractsOnSignComponent,
            data: {
              contractType: CONTRACT_TYPE.ON_SIGN,
            },
          },
          {
            path: CONTRACT_TYPE.APPROVED,
            component: ContractsApprovedComponent,
            data: {
              contractType: CONTRACT_TYPE.APPROVED,
            },
          },
          {
            path: CONTRACT_TYPE.PENDING,
            component: ContractsListBaseComponent,
            data: {
              contractType: CONTRACT_TYPE.PENDING,
            },
          },
          {
            path: CONTRACT_TYPE.REJECTED,
            component: ContractsListBaseComponent,
            data: {
              contractType: CONTRACT_TYPE.REJECTED,
            },
          },
          {
            path: '**',
            redirectTo: CONTRACT_TYPE.ON_SIGN,
          },
        ],
      },
      {
        path: ':id',
        component: ContractsDetailsComponent,
      },
      {
        path: '**',
        redirectTo: 'outcoming',
      },
    ],
  },
  {
    path: '**',
    redirectTo: '',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ContractsRoutingModule {}
